	module.exports = {"data":{"markdownRemark":{"html":"<p>I had made a <a href=\"/work\">ionic version Poolpal app</a> and I'm now on the mission of creating a more modern one with React Native!</p>\n<!-- end -->\n<p>The idea of Poolpal is to save time for people who need to arrange rides often. Say you have a group of 8 going picnic. You have two people driving (phew, that’s good), but you still need to arrange who picks up who so that you have an optimal route. Here’s the Poolpal app comes in handy.</p>\n<p>First screen in the app, you’ll see a map that shows who and where the drivers the passengers are. By default, passengers will be automatically assigned to the driver who is closest to them (it can be changed, of course). Then, the driver can hit the “Let’s go” button and a route will be generated. The driver can choose to open the turn-by-turn navigation in Google Maps (if installed) and the passengers will see a live update of where the drivers are.</p>\n<p>And that’s it!</p>\n<p><em>Wait, what?</em></p>\n<p>Yep, that’s Poolpal. Simple, isn’t it? That’s because that’s the goal of the app--to save you time.</p>\n<p><em>OK, sounds good. So where are you at right now?</em></p>\n<p align=\"center\">\n    <img style=\"width: 380px !important;\" src=\"/2018-09-07_blog_optimized-route-f4176f093cf937d76ed2ee417a346a08.gif\" alt=\"Generating optimized route\">\n    <br>\n    <i>\n      Route optimization - the last pin is the destination,<br>\n      you can see the route changees as the destination chages.\n    </i>\n</p>\n<br>\n<br>\n<p align=\"center\">\n    <img style=\"width: 380px !important;\" src=\"/2018-09-07_blog_ui-8e49acf3691466979464613f589ab25c.gif\" alt=\"Generating optimized route\">\n    <br>\n    <i>User interfaces - Here&apos;s some UI</i>\n</p>","frontmatter":{"date":"September 07, 2018","slug":"/blog/poolpal-to-react-native","title":"Poolpal React-Native-ly"},"timeToRead":1}},"pathContext":{}}

